.container {
    height: max-content;
    padding: 5px 0;
    display: flex;
    align-items: center;
    justify-content: flex-start;
  }
  
  .slider {
    position: relative;
    width: 200px;
    margin-bottom: 25px;
  }
  
  .slider__track,
  .slider__range,
  .slider__left-value,
  .slider__right-value {
    position: absolute;
  }
  
  .slider__track,
  .slider__range {
    border-radius: 3px;
    height: 5px;
  }
  
  .slider__track {
    background-color: #F4F4F4;
    width: 100%;
    z-index: 1;
  }
  
  .slider__range {
    background-color: #EF9655;
    z-index: 2;
  }
  
  .slider__left-value,
  .slider__right-value {
    color: #191919;
    font-size: 12px;
    margin: 20px 0;
  }
  
  .slider__left-value {
    left: 6px;
  }
  
  .slider__right-value {
    right: -4px;
  }
  
  /* Removing the default appearance */
  .thumb,
  .thumb::-webkit-slider-thumb {
    -webkit-appearance: none;
    -webkit-tap-highlight-color: transparent;
  }
  
  .thumb {
    margin-top: -22px;
    pointer-events: none;
    position: absolute;
    height: 0;
    width: 200px;
    outline: none;
  }
  
  .thumb--left {
    z-index: 3;
    margin-left: -2px;
  }
  
  .thumb--right {
    z-index: 4;
  }
  
  /* For Chrome browsers */
  .thumb::-webkit-slider-thumb {
    background-color: #EF9655;
    border: none;
    border-radius: 50%;
    cursor: pointer;
    height: 10px;
    width: 10px;
    margin-top: 5px;
    pointer-events: all;
    position: relative;
  }
  
  /* For Firefox browsers */
  .thumb::-moz-range-thumb {
    background-color: #f1f5f7;
    border: none;
    border-radius: 50%;
    box-shadow: 0 0 1px 1px #ced4da;
    cursor: pointer;
    height: 18px;
    width: 18px;
    margin-top: 4px;
    pointer-events: all;
    position: relative;
  }
  